                    </el-row>
                    <el-row>
                        <el-col :span='20' :offset="2">
                            <div class="basic_exit">
                                <el-row>
                                    <el-col :span='12'>
                                        <el-form ref="form" :model="form" label-width="120px">
                                            <el-form-item label="公司简介：" class='is-required'>
                                                <el-input type='textarea' v-model='post_j' rows='10' maxlength="1000" show-word-limit></el-input>
                                            </el-form-item>
                                            <el-form-item>
                                                <el-button type="primary" @click='saves1'>保存</el-button>
                                            </el-form-item>
                                        </el-form>
