                                </el-row>
                            </div>
                        </el-col>
                    </el-row>
                </div>
                <!-- 企业文化 -->
                <div class="basic_content" v-if='num==2'>
                    <el-row>
                        <el-col>
                            <div class="basic_title">
                                <span>企业文化</span>
                                <span class="r">
                                    <span class='rs'>*</span>
                                    <span>必填项</span>
                                </span>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span='20' :offset="2">
                            <div class="basic_exit">
                                <el-row>
                                    <el-col :span='12'>
                                        <el-form ref="form" :model="form" label-width="120px">
                                            <el-form-item label="企业文化：" class='is-required'>
                                                <el-input type='textarea' v-model='post_q' rows='10' maxlength="1000" show-word-limit></el-input>
                                            </el-form-item>
                                            <el-form-item>
                                                <el-button type="primary" @click='saves2'>保存</el-button>
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                    </el-row>
                </div>
                <!-- 愿景及使命 -->
                <div class="basic_content" v-if='num==3'>
                    <el-row>
                        <el-col>
                            <div class="basic_title">
                                <span>愿景及使命</span>
                                <span class="r">
                                    <span class='rs'>*</span>
                                    <span>必填项</span>
                                </span>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span='20' :offset="2">
                            <div class="basic_exit">
                                <el-row>
                                    <el-col :span='12'>
                                        <el-form ref="form" :model="form" label-width="120px">
                                            <el-form-item label="愿景及使命：" class='is-required'>
                                                <el-input type='textarea' v-model='post_y' rows='10' maxlength="500" show-word-limit></el-input>
                                            </el-form-item>
                                            <el-form-item>
                                                <el-button type="primary" @click='saves3'>保存</el-button>
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                    </el-row>
                </div>
                <!-- 发展历程 -->
                <div class="basic_content" v-if='num==4'>
                    <el-row>
                        <el-col>
                            <div class="basic_title">
                                <span>发展历程</span>
                                <span class="r">
                                    <span class='rs'>*</span>
                                    <span>必填项</span>
                                </span>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span='20' :offset="2">
                            <div class="basic_exit">
                                <el-row>
                                    <el-col :span='12'>
                                        <el-form ref="form" :model="form" label-width="120px">
                                            <el-form-item label="发展历程：" class='is-required'>
                                                <!-- <el-input type='textarea' v-model='post_l' rows='10' maxlength="500" show-word-limit></el-input> -->
                                                <div class="files">+
                                                    <div class="files_left">
                                                        <img :src="image3" alt="" style='widows: 500px;height: 500px;' id="image">
                                                    </div>
                                                    <div class="files_right">
                                                        <input type="file" class="form-control" name='upload' @change="onFileChange3">
                                                    </div>
                                                </div>
                                            </el-form-item>
                                            <el-form-item>
                                                <el-button type="primary" @click='save1'>保存</el-button>
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                    </el-row>
                </div>
                <!-- 组织结构 -->
                <div class="basic_content" v-if='num==5'>
                    <el-row>
                        <el-col>
                            <div class="basic_title">
                                <span>组织结构</span>
                                <span class="r">
                                    <span class='rs'>*</span>
                                    <span>必填项</span>
                                </span>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span='20' :offset="2">
                            <div class="basic_exit">
                                <el-row>
                                    <el-col :span='20'>
                                        <el-form ref="form" :model="form" label-width="100px">
                                            <el-form-item label="组织结构：" class='is-required'>
                                                <div class="files">+
                                                    <div class="files_left">
                                                        <img :src="image" alt="" style='widows: 500px;height: 500px;' id="image">
                                                    </div>
                                                    <div class="files_right">
                                                        <input type="file" class="form-control" name='upload' @change="onFileChange">
                                                    </div>
                                                </div>
                                            </el-form-item>
                                            <el-form-item>
                                                <el-button type="primary" @click='save2'>保存</el-button>
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                    </el-row>
                </div>
                <!-- 企业资质 -->
                <div class="basic_content" v-if='num==6'>
                    <el-row>
                        <el-col>
                            <div class="basic_title">
                                <span>企业资质</span>
                                <span class="r">
                                    <span class='rs'>*</span>
                                    <span>必填项</span>
                                </span>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span='20' :offset="2">
                            <div class="basic_exit">
                                <el-row>
                                    <el-col :span='20'>
                                        <el-form ref="form" :model="form" label-width="100px">
                                            <el-form-item label="企业资质：" class='is-required'>
                                                <div v-for='(i,index) in image2' :key='index'>
                                                    <img :src="i" alt="" style='width:300px;height:300px;float:left'>
                                                </div>
                                                <div>
                                                    <input type="file" class="form-control" name='upload' @change="fileChange" multiple="multiple">
                                                </div>
                                                <!-- <div class="files">+
                                                    <div class="files_left" v-for='(i,index) in image2' :key='index'>
                                                        <img :src="i" alt="" style='widows: 500px;height: 500px;' id="image">
                                                    </div>
                                                    <div class="files_right">
                                                        <input type="file" class="form-control" name='upload' @change="fileChange" multiple="multiple">
                                                    </div>
                                                </div> -->
                                            </el-form-item>
                                            <el-form-item>
                                                <el-button type="primary" @click='save3'>保存</el-button>
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                    </el-row>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import headers from '../../components/header.vue'
export default {
    components: {
        headers
    },
    data() {
        return {
            title: '公司信息管理',
            num: 0,
            post_j: '',
            post_y: '',
            post_l: '',
            post_q: '',
            image: '',
            image2: [],
            image3: '',
            file1: '',
            file2: [],
            file3: '',
            id: '',
            basic_list: [{
                id: 0,
                text: '基本信息'
            }, {
                id: 1,
                text: '公司简介'
            }, {
                id: 2,
                text: '企业文化'
            }, {
                id: 3,
                text: '愿景及使命'
            }, {
                id: 4,
                text: '发展历程'
            }, {
                id: 5,
                text: '组织结构'
            }, {
                id: 6,
                text: '企业资质'
            }],
            form: {
                name: '',
                phone: '',
                email: '',
                address: '',
                route: ''
            },
            rules: {
                name: [{
                    required: true,
                    message: '公司名称不能为空'
                }],
                phone: [{
                    type: 'number',
                    required: true,
                    message: '请输入手机号',
                    trigger: 'blur'
                }, {
                    pattern: /^0{0,1}(13[0-9]|15[7-9]|153|156|18[7-9])[0-9]{8}$/,
                    message: '手机号格式不对',
                    trigger: 'blur'
                }],
                email: [{
                    required: true,
                    message: '请输入邮箱地址',
                    trigger: 'blur'
                }, {
                    type: 'email',
                    message: '请输入正确的邮箱地址',
                    trigger: ['blur', 'change']
                }],
                address: [{
                    required: true,
                    message: '地址不能为空'
                }, {
                    min: 1,
                    max: 50,
                    message: '不能超过50字'
                }],
                route: [{
                    required: true,
                    message: '班车路线不能为空'
                }, {
                    min: 1,
                    max: 200,
                    message: '不能超过200字'
                }]
            }
        }
    },
    methods: {
        save() {
            var formData = new window.FormData();
            var userId = window.sessionStorage.getItem('userId')
            formData.append('companyName', this.form.name)
            formData.append('phone', this.form.phone)
            formData.append('email', this.form.email)
            formData.append('address', this.form.address)
            formData.append('busRoute', this.form.route)
            formData.append('companyProfile', this.post_j) //公司简介
            formData.append('enterpriseCulture', this.post_q) //企业文化
            formData.append('companyVision', this.post_y) //愿景及使命
            formData.append('id', this.id)
            /*formData.forEach((a, b) => {
                console.log(a, b)
            })*/
            this.$axios.post('/tomlive/baseInfo/upload2', formData, {
                headers: {
                    "Content-Type": "multipart/form-data",
                }
            }).then((res) => {
                console.log(res)
                alert('保存成功')
            })
        },
        //发展历程
        save1() {
            var formData = new window.FormData();
            formData.append('companyDevelopment', this.file3);
            formData.append('id', this.id)
            this.$axios.post('/tomlive/baseInfo/companyDevelopment', formData, {
                headers: {
                    "Content-Type": "multipart/form-data",
                }
            }).then((res) => {
                console.log(res)
            })
        },
        //组织结构
        save2() {
            var formData = new window.FormData();
            formData.append('id', this.id)
            formData.append('companyPhilosophy', this.file1);
            this.$axios.post('/tomlive/baseInfo/companyPhilosophy', formData, {
                headers: {
                    "Content-Type": "multipart/form-data",
                }
            }).then((res) => {
                console.log(res)
            })
        },
        save3() {
            var formData = new window.FormData();
            formData.append('id', this.id);
            for (var i = 0; i < this.file2.length; i++) {
                formData.append('companyAptitude', this.file2[i])
            }
            this.$axios.post('/tomlive/baseInfo/companyAptitude', formData, {
                headers: {
                    "Content-Type": "multipart/form-data",
                }
            }).then((res) => {
                console.log(res)
            })
        },
        onFileChange3(e) {
            var files = e.target.files || e.dataTransfer.files;
            console.log(e)
            if (!files.length)
                return;
            this.createImage3(files[0]);
            this.file3 = files[0]
        },
        createImage3(file) {
            var image = new Image();
            var reader = new FileReader();
            var vm = this;
            reader.onload = (e) => {
                vm.image3 = e.target.result;
            };
            reader.readAsDataURL(file);
        },
        fileChange(e) {
            console.log(e)
            var files = e.target.files || e.dataTransfer.files;
            if (!files.length)
                return;
            this.createImage2(files[0])
            this.file2 = files;
            console.log(this.file2)
        },
        createImage2(file) {
            console.log(file)
            var image = new Image();
            var reader = new FileReader();
            var vm = this;
            reader.onload = (e) => {
                vm.image2.push(e.target.result);
            };
            reader.readAsDataURL(file);
            console.log(this.image2)
        },
        onFileChange(e) {
            var files = e.target.files || e.dataTransfer.files;
            if (!files.length)
                return;
            this.createImage(files[0]);
            this.file1 = files[0]
            console.log(this.file1)
        },
        createImage(file) {
            var image = new Image();
            var reader = new FileReader();
            var vm = this;
            reader.onload = (e) => {
                vm.image = e.target.result;
            };
            reader.readAsDataURL(file);
        },
        next() {
            this.num += 1
        },
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // alert('submit!');
                    this.save()
                    // this.$router.push('/home/company_s')
                } else {
                    console.log('error submit!!');
                    return false;
                }
            })
        },
        saves1() {
            this.save()
        },
        saves2() {
            this.save()
        },
        saves3() {
            this.save()
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        fn(index) {
            this.num = index
            /*var a = document.querySelectorAll('.basic_t div')
            a[index].setAttribute('class', 'active')*/
        }
    },
    mounted() {
        this.$axios.post('/tomlive/baseInfo/selectAllBaseInfo').then((res) => {
            console.log(res)
            var arr = res.data.info[0]
            this.form.name = arr.companyName;
            this.form.phone = arr.phone;
            this.form.email = arr.email;
            this.form.phone = arr.phone;
            this.form.address = arr.address;
            this.form.route = arr.busRoute;
            this.post_j = arr.companyProfile;
            this.post_q = arr.enterpriseCulture;
            this.post_y = arr.companyVision;
            this.image3 = arr.companyDevelopment;
            this.image = arr.companyPhilosophy;
            var arr2 = arr.companyAptitude.split(',');
            var _this = this;
            arr2.map(function(val, index) {
                if (val !== "" && val != undefined) {
                    _this.image2.push(val);
                }
            })
            this.id = arr.id
        })
    }
}
</script>
<style scoped>
.main {
    width: 100%;
    height: auto;
}

.basic {
    width: 100%;
    height: 170px;
    background: #fff;
    box-sizing: border-box;
    margin: 20px 0 0 0;
    border: 1px solid #e9e9e9;
}

.basic_content {
    width: 100%;
    min-height: 800px;
    border: 1px solid #e9e9e9;
    box-sizing: border-box;
    margin-top: 40px;
    background: #fff;
}

.basic_t {
    width: 100%;
    height: 150px;
    text-align: center;
    cursor: pointer;
}

.active {
    color: rgba(0, 121, 254, 1);
}

.basic_t>div {
    width: 100%;
    height: 100px;
    line-height: 100px;
}

.basic_t>div>i {
    font-size: 36px;
}

.basic_t>span {
    font-size: 14px;
    font-weight: bold;
    color: #666;
}

.basic_title {
    width: 100%;
    height: 50px;
    color: #666;
    font-size: 14px;
    line-height: 50px;
    font-weight: bold;
    border-bottom: 1px solid #e9e9e9;
    box-sizing: border-box;
}

.basic_title>span {
    margin: 0 20px;
}

.basic_exit {
    width: 100%;
    margin: 40px 0;
}

.next {
    width: 100%;
    height: 40px;
    text-align: center;
}

.next>button {
    padding: 12px 30px;
}

.r {
    float: right;
}

.r>.rs {
    color: red;
    margin: 0 5px;
}

.files {
    width: 500px;
    height: 500px;
    position: relative;
    border: 1px solid #e9e9e9;
    font-size: 188px;
    font-weight: 1;
    color: #e9e9e9;
    text-align: center;
    line-height: 500px;
}

.files>img {
    width: 500px;
    height: 500px;
}

.files_left {
    width: 500px;
    height: 500px;
    position: absolute;
    left: 0;
    top: 0;
}

.files_left>img {
    width: 500px;
    height: 500px;
}

.files_right {
    width: 500px;
    height: 500px;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 999;
}

.files_right>input {
    width: 100%;
    height: 500px;
    opacity: 0;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 999;
}
</style>
<style>
.el-textarea .el-textarea__inner {
    resize: none;
}
</style>